<template>
  <div>
    <section class='teamView__counter'>
      <div
        class='teamView__counter__ally'
        v-for='(value, key) in Object.keys(battleResults)'
        :class='{"teamView__counter__ally--highlight":value === selectedAllyPokemon}'
        :key='key'
        @click='setSelectedAllyPokemon(value)'
      >
        <img :src='`/2d/${value}.png`' />
        <p>{{value}}</p>
      </div>
      <div class='teamView__counter__ally' @click='setSelectedAllyPokemon(-1)'>
        <p>Average</p>
      </div>
    </section>

    <section class='teamView__battle'>
      <section v-show='selectedAllyPokemon'>
        <table class='teamview-table'>
          <tr>
            <th class='teamview-table-head'>Pkmn</th>
            <th class='teamview-table-head'>Item</th>
            <th class='teamview-table-head'>Nature</th>
            <th class='teamview-table-head'>Spread</th>
            <th class='teamview-table-head'>Move</th>
            <th class='teamview-table-head'>Maximum Dmg</th>
          </tr>

          <tr
            v-for='(match, key) in battle(selectedAllyPokemon).slice((start*multiplier)-10, (end*multiplier))'
            :key='key'
          >
            <td>
              <div class='teamview-table-icon'>
                <img :src='`/2d/${match.attacker}.png`' />
              </div>
            </td>
            <td>
              <div class='teamview-table-item'>
                <img :src='`/items/${match.item}.png`' />
              </div>
            </td>
            <td>
              <div
                class='teamview-table-nature'
              >{{match.nature}}</div>
            </td>
            <td>
              <div class='teamview-table-spread'>
                <p
                  v-for='(ev, key) in match.spread'
                  :key='key'
                  :class='`${stats[key]}-lighter`'
                >{{ev}}</p>
              </div>
            </td>
            <td>
              <div class='teamview-table-move'>{{match.move}}</div>
            </td>
            <td>
              <div
                class='teamview-table-damage'
              >{{Number(match.damageTaken).toFixed(1)}}%</div>
            </td>
          </tr>
        </table>

        <section class='center center--row'>
          <div class='dexview__slider dexview__slider--med'>
            <a class='dexview__slider__back' @click='go(-1)'></a>
            <div class='dexview__slider__box'>
              <p>{{end*multiplier}} / {{battleSize}}</p>
            </div>
            <a class='dexview__slider__go' @click='go(1)'></a>
          </div>

          <a
            class='dexview__slider dexview__slider--ball dexview__slider--config'
            @click='resetCalculator()'
          ></a>
          <a
            class='dexview__slider dexview__slider--ball dexview__slider--add'
            @click='addToCalculation()'
          ></a>
          <a
            class='dexview__slider dexview__slider--ball dexview__slider--reset'
            @click='resetCalculator()'
          ></a>
        </section>
      </section>
    </section>

    <transition name='bounce'>
      <Modal
        v-if='pokemonListModal'
        transition='bounce'
        @close-my-modal='pokemonListModal = false'
        :sizeClass='"modal__content--large"'
      >
        <PokemonList
          @close-my-modal='pokemonListModal = false'
          @update-damage-table='multiplier=1'
          :pokemonKeys='pokemonKeys'
          :tier='data.tier'
          :team='this.data.pokemon'
        ></PokemonList>
      </Modal>
    </transition>
  </div>
</template>

<script>
import Modal from "@/components/Modal";
import PokemonList from "./Slots/_PokemonList";
import battleMixin from "@/mixins/battleMixin.js";

import axios from "axios";

export default {
  mixins: [battleMixin],
  props: ["data"],
  components: {
    Modal,
    PokemonList
  },
  data() {
    return {
      start: 10,
      end: 10,
      multiplier: 1,
      pokemonKeys: [],
      pokemonListModal: false,
      stats: ["hp", "atk", "def", "spa", "spd", "spe"]
    };
  },
  methods: {
    openView(pkmn) {
      const tier = this.data.tier;
      console.log(tier);
      const routeData = this.$router.resolve({
        name: "pokemon",
        params: { tier: tier, pokemon: pkmn }
      });
      window.open(routeData.href, "_blank");
    },
    go(value) {
      value = this.multiplier + value;
      if (value <= 0) {
        return;
      }
      if (value > Math.ceil(this.battleSize / 10)) {
        return;
      }
      this.multiplier = value;
    },
    addToCalculation() {
      this.pokemonListModal = true;
      const tier = this.data.tier;
      axios.get(`/battlepark/${tier}`).then(res => {
        if (res.data.code) {
          this.pokemonKeys = res.data.keys;
        }
      });
    },
    resetCalculator() {
      this.setBattleResults(
        this.data.weak,
        this.data.pokemon,
        this.$route.params.tier
      );
      this.multiplier = 1;
    }
  },
  computed: {
    battle(){
      return this.$store.getters['battle/getBattleResultsByKey']
    }
  },
  created() {
    this.setBattleResults(
      this.data.weak,
      this.data.pokemon,
      this.$route.params.tier,
    );
  }
};
</script>


